/* ============ desktop view ============ */
@media all and (min-width: 992px) {

	.dropdown-menu li{
		position: relative;
	}
	

	.dropdown-content .dropdown-menu{ 
		display: block;
		position: static;
		margin-bottom: 5px;		
		padding-top: 0px;
		border: none;
	}

	
	.dropdown-content .submenu{ 
		display: none;
		position: absolute;
		left:100%; top:-7px;
	}
	.dropdown-content .submenu-left{ 
		right:100%; left:auto;
	}

