.demo_switcher {
	position: fixed;
	right: 0;
	z-index: 9999;
	top: 20%;
}
.demo > ul > li > a {
	font-size: 22px;
	text-align: center;
	display: block;
	width: 40px;
	height: 40px;
	line-height: 40px;
	color: #fff;
	transition: all 0.5s ease-in-out;
}
.demo > ul > li > a.menu_icon {
	line-height: 44px;
	font-size: 25px;
}
.bg-green {
	background-color: #73e080;
}
.bg-red {
	background-color: #f94551;
}
.demo_list ul {
	list-style: none;
	display: inline-block;
}
.demo_list {
	background-color: #fff;
	position: fixed;
	right: -360px;;
	max-width: 360px;
	padding: 5px;
	overflow: auto;
	top: 0;
	bottom: 0;
	transition: all 0.5s ease-in-out;
	z-index: 9999;
}
.demo_list.switch-active {
	right: 0;
}
.demo_list li {
	float: left;
	 width: 50%;
	 padding: 5px;
}
.demo_list li a {
	display: block;
	border: 1px solid #ddd;
	padding: 4px;
	position: relative;
}
.demo_list li a::before {
	content: "";
	position: absolute;
	left: 4px;
	top: 4px;
	bottom: 4px;
	right: 4px;
	opacity: 0;
	background-color: rgba(0,0,0,0.6);
	z-index: 9;
	transition: all 0.5s ease-in-out;
}
.demo_list li a:hover:before {
	opacity: 1;
}
body.switch-open {
	overflow: hidden;
	padding-right: 15px;
}
.switch-open::before {
	content: "";
	position: fixed;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	background-color: rgba(0,0,0,0.8);
	z-index: 9999;
}
.demo li {
	position: relative;
}
.demo > ul > li > a.demo_icon.switch-active {
	position: relative;
	transition: all 0.5s ease-in-out;
	-webkit-transform: translateX(-360px);
	transform: translateX(-360px);
}
.demo > ul > li > a.demo_icon.switch-active i::before {
	content: "\f129";
	font-size: 20px;
	vertical-align: middle;
}
.demo > ul > li > a.menu_icon.switch-active {
	line-height: 36px;
}
.demo_list h5 {
	margin: 10px 0;
}
.demo li.switch-active .color_icon {
	transition: all 0.5s ease-in-out;
	-webkit-transform: translateX(-200px);
	transform: translateX(-200px);
}
.color-switch {
	background: #212121;
	right: -200px;
	padding: 10px;
	position: absolute;
	top: 0;
	transition: all .5s ease-in-out;;
	width: 200px;
	z-index: 999;
}
.switch-active {
	right: 0;
}
.color-switch button::after {
	color: #ffffff;
	content: "\f122";
	font-family: "Ionicons";
	font-size: 18px;
	left: 5px;
	opacity: 0;
	position: absolute;
	top: 2px;
}
.color-switch button.active::after {
	opacity: 1;
}
.color-switch button {
	border: 0 none;
	border-radius: 3px;
	cursor: pointer;
	height: 25px;
	line-height: 20px;
	margin: 2px;
	padding: 0;
	position: relative;
	text-align: center;
	width: 25px;
}
